import React from 'react';
import { FontandbackgroundcolorNormal } from '@ali/we-icon';
import { isGradientColor, transferGradientColor } from '@ali/4ever-utils';
interface IFontIcon {
  color: string;
  backgroundColor: string;
}

const FontColorIcon: React.FC<IFontIcon> = (props) => {
  const { color, backgroundColor } = props;

  return isGradientColor(color) ? (
    <GradientColorFontIcon color={color} backgroundColor={backgroundColor} data-role="font_color_gradient" />
  ) : (
    <FontandbackgroundcolorNormal
      style={{
        color,
        backgroundColor,
        borderRadius: 2,
      }}
      data-role="font_color_normal"
    />
  );
};

const GradientColorFontIcon: React.FC<IFontIcon> = (props) => {
  const { color, backgroundColor } = props;
  const [startColor, endColor] = transferGradientColor(color);
  return (
    <svg
      width="20"
      height="20"
      viewBox="0 0 20 20"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
      style={{ backgroundColor, borderRadius: 2 }}
    >
      <rect
        x="3"
        y="16"
        width="14"
        height="2"
        fill={`url(#paint0_linear_${startColor}:${endColor})`}
      />
      <path
        d="M7.53848 10L5.93848 14H4.86145L9.53573 2.31433H10.4642L15.1385 14H14.0615L12.4615 10H7.53848ZM7.93848 9.00003H12.0615L9.99997 3.84632L7.93848 9.00003Z"
        fill={`url(#paint0_linear_${startColor}:${endColor})`}
      />
      <defs>
        <linearGradient
          id={`paint0_linear_${startColor}:${endColor}`}
          x1="4"
          y1="9.5"
          x2="16"
          y2="9.5"
          gradientUnits="userSpaceOnUse"
        >
          <stop stopColor={startColor} />
          <stop offset="1" stopColor={endColor} />
        </linearGradient>
      </defs>
    </svg>
  );
};

export default FontColorIcon;
